import * as React from "react";
import { Link, HeadFC } from "gatsby";

import SkeletonVideo from "../media/nokia-espionage.mp4";

const NotFoundPage = () => {
	return (
		<div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
			<style>{background}</style>
			<div style={{ position: 'fixed', top: '30vh' }}>
				<Link to="/">
					<video width="100%" height="100%" preload="auto" autoPlay loop muted>
						<source src={SkeletonVideo} type="video/mp4"/>
					</video>
				</Link>
			</div>
		</div>
	)
}

const background = `
	:root {
		background-color: black;
	}
`

export default NotFoundPage

export const Head: HeadFC = () => <title>whoops</title>
